import Accordion from 'modules/accordion'
import {select, selectAll, getCollection} from 'lib/selectors'

function Contact(el) {
  // eslint-disable-next-line no-new
  new Accordion(el)
  
  const contactCTA = selectAll('.accordion .is-underline')
  contactCTA.forEach((cta) => {
    cta.addEventListener('click', analyticsTabCallback)
  })
  function analyticsTabCallback(e) {
    const eventCategory = select('.is-active h2').innerText
    let currentTarget = e.currentTarget.attributes['data-link-type']
    const eventActionAttr = (currentTarget) ? currentTarget.value.toLowerCase() : null
    let eventAction = ''
    let eventLabel = ''
  
    if(eventActionAttr) {
        if ( eventActionAttr == "call" ) {
            eventAction = 'customer service'
        } else if ( eventActionAttr == "email" ) {
            eventAction = 'contact us'
        } else {
            eventAction = 'live chat'
            eventLabel = 'open'
        }
    }

    window.document.DLKit.push('when-user-interact-with-contact-page', {
        eventCategory: eventCategory,
        eventAction: eventAction,
        eventLabel: eventLabel
    })
  }
  /*
  * Binding click event on <p> element of accordion header
  */
  const collapseEls = getCollection('.heading-subtext', el)
  collapseEls.forEach(collapseEl => collapseEl.addEventListener('click', handleCollapse))
  function handleCollapse(e) {
    let elementBtn = e.currentTarget.parentElement.childNodes[1].childNodes[1]
    elementBtn.click()
  }
}

export default function (el) {
  return new Contact(el)
}